import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";

const SService = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 15px;
  padding: 49px;
  width: 50%;
  max-width: 600px;

  background: linear-gradient(90.7deg, #f4faff 8.32%, #fbfdff 99.04%), #0079e7;
  border-radius: 44px;

  & button {
    background: radial-gradient(
      105.43% 294.48% at -10.2% -116.67%,
      #376ce0 0%,
      #2485dd 25.96%,
      #4f4ed9 74.99%,
      #7320cf 96.87%
    );
    box-shadow: inset 0px 0px 37px -11px rgba(255, 255, 255, 0.29);
    border-radius: 34.5px;
  }

  @media (max-width: 1200px) {
    width: 100%;
  }
`;

const Images = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 244px;
  gap: 20px;

  overflow: hidden;
`;

const Title = styled(Title3)`
  display: flex;
  gap: 24px;

  font-size: clamp(39px, 4vw, 54px);
  font-weight: 400;
  line-height: 130%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  margin-top: 75px;
`;

const SText = styled(Text)`
  margin-bottom: 35px;
`;

const Img = styled.img`
  width: 100%;
  max-width: 441px;
`;

const Service = ({ title, desc, btnText, imgs }) => {
  return (
    <SService>
      <Images>
        {imgs.map((img) => (
          <Img
            key={Math.random()}
            width={img.width}
            height={img.height}
            loading="lazy"
            alt="Service_img"
            src={img.img}
          />
        ))}
      </Images>
      <Title>{title}</Title>
      <SText size="20" color="#3C3C3C;">
        {desc}
      </SText>
      <Button type="service" colorTxt="#E7F5FF" link={true}>
        {btnText}
      </Button>
    </SService>
  );
};

export default Service;
